.button {
  width: 300px;
  height: 80px;
  border: 1px solid $primary;
  font-size: 1.8rem;
  letter-spacing: 0.2em;
  position: relative;
  overflow: hidden;
  text-align: center;
  @include center;
  @include sp {
    width: 100%;
    max-width: 500px;
    height: getsp375(120px);
    font-size: getsp375(2.6rem);
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    border-top: 15px solid $primary;
    border-left: 15px solid transparent;
    transition: all 0.5s ease-in-out;
    @include sp {
      border-top-width: getsp375(31px);
      border-left-width: getsp375(31px);
    }
  }

  span {
    position: absolute;
    z-index: 1;
  }

  &:hover {
    &::before {
      border-top-width: 150px;
      border-left-width: 700px;
    }

    span {
      color: $white;
    }
  }

  &--white {
    border: 1px solid $white;
    @include sp {
      width: 100%;
      max-width: 200px;
    }

    &::before {
      border-top: 15px solid $white;
      @include sp {
        border-top-width: getsp375(31px);
      }
    }

    span {
      color: $white;
    }

    &:hover {
      span {
        color: $primary;
      }
    }
  }

  &--about {
    width: 300px;
    height: 50px;
    font-size: 1.2rem;
    @include sp {
      width: 100%;
      max-width: 500px;
    }
  }
}

.button-link {
  font: 1.6rem;
  line-height: 1em;
  letter-spacing: 0.2em;
  cursor: pointer;
  @include sp {
    font-size: getsp375(2.6rem);
  }

  &::after {
    content: '';
    display: inline-flex;
    width: 12px;
    height: 12px;
    background-color: $primary;
    border-radius: 6px;
    margin-left: getpc(19px);
    transition: 0.3s ease;
  }

  &:hover {
    &::after {
      margin-left: 6px;
    }
  }

  &--more {
    @include center;

    &::after {
      content: '';
      display: inline-flex;
      width: 15px;
      height: 15px;
      // background: url('/img/icon-plus.png') no-repeat 50% 50% / cover;
      border-radius: 0;
      margin: 1px 0 0 60px;
      position: relative;
      top: 50%;
      @include sp {
        width: 12px;
        height: 12px;
        margin-left: getsp375(60px);
      }
    }

    &:hover {
      &::after {
        margin-left: 60px !important;
        transform: rotate(90deg);
        @include sp {
          margin-left: getsp375(60px) !important;
        }
      }
    }
  }
}

.button-about {
  width: 100%;
  height: 80px;
  border: 1px solid $primary;
  font-size: 1.8rem;
  letter-spacing: 0.2em;
  position: relative;
  overflow: hidden;
  text-align: center;
  background-color: $primary;
  color: $white;
  @include center;
  @include sp {
    width: 100%;
    max-width: 500px;
    height: getsp375(120px);
    font-size: getsp375(2.6rem);
  }

  &:hover {
    transition: 0.3s ease-in;
    background-color: $white;

    span {
      color: $primary;
    }
  }
}

.button-contact {
  width: 100%;
  height: 50px;
  border: 1px solid $gold;
  font-size: 1.8rem;
  letter-spacing: 0.2em;
  position: relative;
  overflow: hidden;
  text-align: center;
  background-color: $gold;
  color: $white;
  padding: 5px;
  @include center;
  @include sp {
    width: 100%;
    max-width: 500px;
    height: getsp375(90px);
    font-size: getsp375(2.6rem);
  }

  &:hover {
    transition: 0.3s ease-in;
    background-color: $white;

    span {
      color: $primary;
    }
  }
}

.button-map-info {
  width: 100%;
  height: 50px;
  border: 1px solid $gold;
  font-size: 1.8rem;
  letter-spacing: 0.2em;
  position: relative;
  overflow: hidden;
  text-align: center;
  background-color: none;
  color: $white;
  padding: 5px;
  @include center;
  @include sp {
    width: 66.66%;
    max-width: 500px;
    height: getsp375(120px);
    font-size: getsp375(2.6rem);
  }

  &:hover {
    transition: 0.3s ease-in;
    background-color: $gold;

    span {
      color: $primary;
    }
  }
}

.button-about-information {
  position: relative;
  padding: 10px 40px;
  background: none;
  border: none;
  font-weight: $regular;
  font-size: 16px;
  letter-spacing: 3px;
  background-color: $gold;
  color: $white;
  cursor: pointer;
  @include center;
  &:after,
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    border: 2px solid $gold;
    transition: transform .2s;
  }
  &:after {
    transform: translate(6px, 6px);
  }
  &:before {
    transform: translate(-6px, -6px);
  }
}


.button-service {
  width:300px;
  height: 60px;
  border-radius: 50px;
  font-size: 1.8rem;
  letter-spacing: 0.2em;
  position: relative;
  overflow: hidden;
  text-align: center;
  background-color: $gold;
  color: $white;
  @include center;
  @include sp {
    width: 66.66%;
    max-width: 500px;
    height: getsp375(120px);
    font-size: getsp375(2.6rem);
  }

  &:hover {
    transition: 0.3s ease-in;
    background-color: #978f78;
    border: 1px solid $gold;
  }
}