.slideshow#slideshow {
  overflow: hidden;
	position: relative;
	width: 100%;
	height: 100vh;
  z-index: 1;
  @include sp {
    height: 100vh;
    padding: 0 4%;
  }
  // @include tablet {
  //   height: 100vh;
  // }
  // @include small-pc {
  //   height: 100vh;
  // }

  &::before {
    content: ''; // ::before and ::after both require content
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('/img/slideshow-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 9;
    @include sp {
      background-size: 100% 100%;
      background-image: url('/img/slideshow-bg-sp.png');
    }
  }

  figcaption {
    display: block;
    margin-left: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 40%;
    z-index: 10;
    font-family: $playfair;
    color: $white;
    text-align: left;
    @include sp {
      width: 100%;
      margin: auto;
      text-align: center;
    }

    h1 {
      font-size: 10rem;
      line-height: getem(10, 10);
      @include sp {
        font-size: getsp375(50px);
      }
    }

    h2 {
      font-size: 5rem;
      @include sp {
        font-size: getsp375(50px);
      }
    }

    p {
      font-family: $source;
      color: $gold;
      font-size: 3rem;
      @include sp {
        font-size: getsp375(40px);
      }
    }
  }

  figure {
    margin: 0;
    top:0;
    bottom: 0;
    pointer-events: none;
    display: flex;
    opacity: 0;
    transition: all 0.5s ease;

    img {
      display: block;
      margin: auto;
      width: 100%;
      max-width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      animation: zoom-out 10s linear infinite;
    }

    &.show {
      opacity: 1;
      pointer-events: initial;

      figcaption {
        right: 2%;
        animation: slideRight 1s;
      }
    }
  }
}

/* navigation size */
#slideshow ul a{
	width: 10px;
	height: 10px;
  z-index: 10;
}

/* colors */
#slideshow,#slideshow figure{
	background: $white;
}

#slideshow figure figcaption{
  position: absolute;
  top: 0;
  height: 100vh;
  width: 100vw;
}

#slideshow ul a{
  border: 2px solid $white;
	background: $primary;
}
#slideshow ul a.slide{
  width: 20px;
  height: 20px;
	background: $primary;
}
#slideshow ul a:hover{
	background: $gold;
}

#slideshow figure figcaption, #slideshow ul, #slideshow figure{
	position: absolute;
	right: 0;
	left: 0;
	text-align: center;
}

#slideshow ul{
	z-index: 2;
	margin: 10px;
	bottom: 0;
	display: -webkit-flex;
	display: flex;
  align-items: center;
	justify-content: flex-end;
  z-index: 10;
}
#slideshow ul a{
	margin: 0 3px;
	border-radius: 50%;
	transition: all 0.3s ease;
  @include center;
}

@keyframes zoom-out {
  0% {
      transform: scale(1,1);
      opacity: 0.8
  }
  100% {
    transform: scale(1.2,1.2);
    opacity: 1
  }
}

@keyframes slideRight {
  0% {
    right: -100%;
  }

  100% {
    right: 2%;
  }
}