.covid-response {
  margin: 145px auto 40px;
  width: 1318px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    margin: 60px auto;

    &--reverse {
      flex-direction: row-reverse;
      margin: 60px auto;
    }
  }

  &__info {
    width: 765px;

    // &::before {
    //   content: '';
    //   background-image: url('/img/sectionbg/covid-response-bg.png');
    //   background-size: contain;
    //   background-repeat: no-repeat;
    //   background-position: right;
    //   position: absolute;
    //   top: 0;
    //   right: 0;
    //   bottom: 0;
    //   left: 0;
    //   opacity: 0.2;
    // }
  }

  &__image-container {
    width: 670px;
    @include center;
    flex-direction: column;
    margin-left: 30px;
    z-index: 2;

    &--sec {
      width: 500px;
      margin-left: 0;
      margin-right: 40px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }
  }

  &__info-text {
    font-size: 1.8rem;
    text-align: justify;

    &:not(:last-child) {
      margin-bottom: 45px;
    }

    span {
      font-size: 2rem;
      color: $primary;
    }

    &--white {
      color: $white;
    }

    &--bold-white {
      // font-family: $source;
      color: $white;
      font-weight: $bold;
      text-transform: capitalize;
    }

    &--gray {
      color: $gray;
    }
  }

  &__name {
    font-family: $playfair;
    font-size: 2.5rem;
    color: $primary;
    margin: 15px auto;
  }

  &__list {
    margin: 20px 0;

    li {
      font-size: 1.8rem;
      list-style-type: disc;
    }
  }

  &__nested-list {
    margin: 10px 0 10px 20px;

    li {
      font-size: 1.8rem;
      list-style-type: circle;
    }
  }
}