.service-1 {
  position: relative;

  &__hero {
    background-image: url('/img/service-1-hero.jpg');
    background-attachment: fixed;
    background-position-y: -0.5;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    color: white;
    transition: 0s linear;
    transition-property: background-position;
    width: 100%;
    height: 550px;
    @include center;
    flex-direction: column;
    box-shadow:inset 0 0 0 2000px rgba(255, 255, 255, 0.140);
    @include sp {
      height: 100vh;
    }
  }

  &__hero-text-wrapper {
    display: flex;
    justify-content: space-between;
    width: 1000px;
    margin-top: 50px;
    @include sp {
      width: 100%;
      margin-top: 25px;
      justify-content: center;
      flex-direction: column;
    }
  }

  &__hero-text {
    font-size: 4.2rem;
    font-weight: $bold;
    color: $black;
    @include sp {
      font-size: 3.2rem;
    }

    &--big {
      font-size: 5.8rem;
      font-weight: $regular;
      @include sp {
        font-size: 5rem;
      }
    }

    &--small {
      font-size: 3.4rem;
      @include sp {
        font-size: 3rem;
      }  
    }
  }

  &__hero-button-container {
    margin-top: 100px;
    width: 330px;
    @include sp {
      margin-top: 50px;
      width: 250px;
    }
  }
}