.footer {
  position: relative;
  bottom: 0;

  &__copyright {
    position: relative;
    bottom: 0;
    width: 100%;
    padding: 15px;
    background-color: $black;
    @include center;
    @include sp {
      padding: getsp375(20px);
      text-align: center;
    }
  }

  &__copyright-wrapper {
    display: flex;
    justify-content: space-between;
    width: 1170px;
    @include sp {
      width: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__copyright-text {
    font-family: $source;
    font-size: 1.2rem;
    color: $white;
  }

  &__image {
    width: 100%;
    padding: 20px;
    background-color: white;

    &:hover {
      transition: 0.3s ease-in-out;
      opacity: 0.8;
    }

    @include sp {
      padding: getsp375(20px);
      width: 100%;
    }
  }

  &__container {
    background-color: $primary;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    @include sp {
      flex-direction: column;
    }
  }

  &__section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 400px;
    padding: 90px 35px;
    @include sp {
      width: 100%;
      padding: getsp375(90px) getsp375(35px) 0;
    }

    &:nth-child(2) {
      border-left: 1px solid $white;
      border-right: 1px solid $white;
    }
  }

  &__section-wrap {
    display: flex;
    justify-content: center;
    flex-direction: column;
  }

  &__section-text {
    font-family: $source;
    font-size: 2rem;
    font-weight: $semibold;
    line-height: getem(2, 2.8);
    color: $white;
    @include sp {
      font-size: getsp375(32px);
    }

    &:not(:last-child) {
      margin-bottom: 20px;
      @include sp {
        margin-bottom: getsp375(20px);
      }
    }

    &.sublink {
      font-size: 1.4rem;
      font-weight: $regular;
      margin-left: 20px;
      @include sp {
        margin-left: getsp375(20px);
      }
    }

    a {
      &:hover {
        transition: 0.3s ease-in-out;
        color: $gold;
      }
    }

    ul {
      margin: 20px 0 0;
      @include sp {
        margin: getsp375(20px) 0 0;
      }
    }
  }

  &__title {
    font-family: $source;
    font-size: 2.5rem;
    font-weight: $semibold;
    line-height: getem(16px, 28px);
    color: $white;
    padding-bottom: 20px;
    @include sp {
      font-size: getsp375(40px);
    }
    
    &::after {
      content: '';
      display: block;
      height: 3px;
      width: 30px;
      background-color: $gold;
    }
  }

  &__about {
    font-family: $source;
    font-size: 1.6rem;
    line-height: getem(16px, 28px);
    color: $white;
    text-align: justify;
    margin-top: 20px;
    @include sp {
      font-size: getsp375(32px);
      margin-top: getsp375(20px);
    }
  }

  &__contact {
    display: flex;
    margin-top: 20px;
    @include sp {
      margin: getsp375(20px) 0 getsp375(40px);
    }

    img {
      width: 30px;
      height: 30px;
      margin-right: 15px;
      @include sp {
        width: getsp375(30px);
        height: getsp375(30px);
        margin-right: getsp375(15px);
        margin-top: getsp375(10px);
      }
    }

    .footer__section-text:hover {
      transition: 0.3s ease-in-out;
      color: $gold;
    }
  }

  &__socmed {
    display: flex;
    justify-content: space-evenly;  
    width: 100%;
    height: 30px;
    margin: 20px auto;

    .fb, 
    .twt, 
    .ig, 
    .yt, 
    .ln {
      width: 40px;
      height: 40px;
      @include center;

      &:hover {
        border-radius: 50%;
      }
    }

    .fb {
      &:hover {
        background-color: #3b5998;
      }
    }

    .twt {
      &:hover {
        background-color: #1da1f2;
      }
    }

    .ig {
      &:hover {
        background-color: #e1306c;
      }
    }

    .yt {
      &:hover {
        background-color: #ff0000;
      }
    }

    .ln {
      &:hover {
        background-color: #0e76a8;
      }
    }

    img {
      width: 20px;
      height: 20px;
    }
  }
}