.white {
  color: $white;
}

.white-bg {
  background: $white;
}

.black {
  color: $black;
}

.gold {
  color: $gold;
}


.black-bg {
  background: $black;
}

.primary {
  color: $primary;
}

.primary-bg {
  background: $primary;
}

.overflow {
  overflow: hidden;
}

.italic {
  font-style: italic;
}

.underline {
  text-decoration: underline;
}

.bold {
  font-weight: $bold;
}

.italic-bold {
  font-style: italic;
  font-weight: $bold;
}

.arrow {
  width: 0;
  height: 0;
  border: 0 solid transparent;
  border-left-width: 5px;
  border-right-width: 5px;
  border-top: 10px solid $gold;
  margin: 0 0 0 5px;
}

.pc-only {
  @include sp {
    display: none;
  }
}

.sp-only {
  display: none;
  @include sp {
    display: block;
  }
}