.wills-trusts {
  margin: 145px auto 40px;
  @include sp {
    margin: 100px auto 20px;
    padding: 0 4%;
    width: 100%;
  }

  &__wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    margin: 60px auto;
    @include sp {
      margin: 30px auto 10px;
      flex-direction: column;
      justify-content: center;
    }
  }

  &__info {
    width: 765px;
    @include sp {
      width: 100%;
    }

    &::before {
      content: '';
      background-image: url('/img/hex.png');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: right;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 0.2;
    }
  }

  &__image-container {
    width: 600px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    margin-left: 30px;
    z-index: 2;
    @include sp {
      width: 100%;
      height: auto;
      margin-left: 0;
      margin-top: 20px;
    }
  }

  &__image {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__info-text {
    font-size: 1.8rem;
    text-align: justify;
    @include sp {
      font-size: 1.6rem;
    }

    &:not(:last-child) {
      margin-bottom: 45px;
      @include sp {
        margin-bottom: 25px;
      }
    }

    span {
      font-size: 2rem;
      color: $primary;
      @include sp {
        font-size: 1.6rem;
      }
    }

    &--white {
      color: $white;
    }

    &--bold-white {
      // font-family: $source;
      color: $white;
      font-weight: $bold;
      text-transform: capitalize;
    }

    &--gray {
      color: $gray;
    }
  }

  &__title-wrap {
    margin: 10px auto;
    text-align: center;
  }

  &__faq {
    margin: 100px 0;
    width: 100%;
    @include sp {
      margin: 50px 0;
    }

    &--small {
      margin: 20px 0;
      width: 350px;
      @include sp {
        margin: 10px 0;
        width: 100%;
      }
    }
  }

  &__faq-item {
    padding: 22px 40px 30px;
    margin-bottom: 20px;
    background-color: $white;
    border-bottom: 1px solid $gray;
    @include sp {
      padding: 14px 20px;
      margin-bottom: 15px;
    }

    &--animate {
      .wills-trusts__faq-arrow {
        transform: rotate(360deg) scale(-1);
      }

      .wills-trusts__faq-item-answer {
        transition: 1s ease-in-out;
        max-height: 100vh;
      }
    }

    &--small {
      padding: 20px;
      border: none;
      box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;
      @include sp {
        padding: 15px;
      }
    }
  }

  &__faq-item-question {
    cursor: pointer;
    display: flex;
    align-items: center;
  }

  &__faq-question {
    margin-right: auto;
    font-size: 2.4rem;
    font-family: $roboto;
    font-weight: $bold;
    color: $primary;
    text-align: left;
    @include sp {
      font-size: 2rem;
    }

    &--small {
      font-size: 1.6rem;
    }
  }

  &__faq-arrow {
    width: 20px;
    height: 20px;
    margin-left: 20px;
    transition: .5s ease-in-out;
    @include sp {
      margin-left: 10px;
      width: 15px;
      height: 15px;
    }

    &--small {
      width: 15px;
      height: 15px;
      margin-left: 10px;
    }
  }

  &__faq-item-answer {
    max-height: 0;
    padding-right: 20px;
    overflow: hidden;
    display: flex;
    align-items: center;
    transition: 0.8s ease-in-out;
    @include sp {
      padding-right: 10px;
    }
  }

  &__faq-answer {
    padding-top: 35px;
    margin-right: auto;
    font-size: 1.8rem;
    line-height: getem(1.6, 3.2);
    text-align: justify;
    @include sp {
      padding-top: 15px;
      font-size: 1.6rem;
    }

    &--small {
      padding-top: 15px;
      font-size: 1.6rem;
      @include sp {
        padding-top: 10px;
      }
    }
  }

  &__diff {
    margin: 20px auto;
    @include sp {
      margin: 10px auto;
    }
  }

  &__diff-wrap {
    display: flex;
    justify-content: space-between;
    margin: 20px auto;
    @include sp {
      flex-direction: column;
      justify-content: center;
      margin: 10px auto;
    }
  }

  &__diff-item {
    width: calc(50% - 20px);
    @include sp {
      width: 100%;
    }
  }

  &__list {
    margin: 20px auto;
    @include sp {
      margin: 10px auto;
    }
  }

  &__list-item {
    margin: 20px 0 20px 20px;
    list-style-type: disc;
    font-size: 2rem;
    font-family: $roboto;
    @include sp {
      margin: 20px 0 20px 25px;
      font-size: 1.8rem;
    }
  }
}
