.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 105px;
  padding: 0 0 0 20px;
  @include center;
  font-size: 1.8rem;
  line-height: getem(18, 22);
  background-color: rgba(255, 255, 255, 0.555);
  left: 0;
  display: flex;
  align-items: center;
  z-index: 999;
  @include sp {
    height: 50px;
    padding: 0 0 0 getsp375(20px);
  }
  @include tablet {
    height: 50px;
    padding: 0 0 0 getsp375(20px);
  }
  @include small-pc {
    height: 50px;
    padding: 0 0 0 getsp375(20px);
  }

  &--fixed {
    background-color: $white !important;
    position: fixed !important;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
    animation: fade-in 0.8s ease-in-out;
  }

  &__logo {
    width: 300px;
    height: 70px;
    margin-right: auto;
    @include center;
    @include sp {
      width: 150px;
    }
    @include tablet {
      width: 150px;
    }
    @include small-pc {
      width: 150px;
    }
    
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }      
  }

  &-nav {
    display: flex;
    @include sp {
      display: none;
    }
    @include tablet {
      display: none;
    }
    @include small-pc {
      display: none;
    }
  }

  &-nav__link {
    position: relative;
    padding: 0 20px;
    height: 50px;
    color: $text;
    cursor: pointer;
    @include center;

    p {
      --border-color: #cca76f;
      --border-width: 2px;
      --bottom-distance: 0px;
      
      position: relative;
      color: $primary;
      display: inline-block;
      background-image: linear-gradient(var(--border-color), var(--border-color));
      background-size: 0% var(--border-width);
      background-repeat: no-repeat;
      transition: background-size 0.3s;
      margin: 7px 0;
      background-position: 50% calc(100% - var(--bottom-distance));

    &:hover {
      background-size: 100% var(--border-width);
    }
  }
    

    &:nth-child(2):hover {
      .header-nav__submenu {
        max-height: 300px;
      }
    }
  }

  &-nav__submenu {
    position: absolute;
    max-height: 0;
    height: 300px;
    width: 400px;
    color: $white;
    background-color: $primary;
    top: 77px;
    left: -100%;
    transition: .5s;
    overflow: hidden;  
    @include center;
    flex-direction: column;
  }

  &-nav__submenu-link {
    position: relative;
    font-size: 2rem;
    line-height: getem(20, 35);
    font-weight: $black;
    letter-spacing: 0.1em;

    h3 {
      font-size: 1.8rem;
      font-weight: $light;
      line-height: getem(18, 31.5);
      text-align: center;

      &:hover {
        transition: 0.3s ease-in;
        background-color: $gold;
      }
    }

    &-container {
      display: flex;
      justify-content: flex-start;
      flex-direction: column;
    }
  }
    
  &-container {
    display: flex;
    flex-direction: column;
  }

  &-menu__btn {
    position: relative;
    display: none;
    cursor: pointer;
    @include sp {
      width: getsp375(100px);
      height: 100%;
      background-color: $primary;
      flex-direction: column;
      @include center;
    }
    @include tablet {
      width: getsp375(100px);
      height: 100%;
      background-color: $primary;
      flex-direction: column;
      @include center;
    }
    @include small-pc {
      width: getsp375(100px);
      height: 100%;
      background-color: $primary;
      flex-direction: column;
      @include center;
    }

    &-shape {
      width: getsp375(45px);
      height: 1.5px;
      background-color: $white;
      transition: .5s;

      &:not(:last-child) {
        margin-bottom: getsp375(10px);
      }
    }
  }

  &-menu__btn-animate {
    .header-menu__btn-shape {
      position: absolute;
      top: 50%;
      left: 30%;
      transform: translateY(-50%, -30%);

      &:first-child {
        transform: rotate(45deg);
      }

      &:nth-child(2) {
        opacity: 0;
      }

      &:nth-child(3) {
        transform: rotate(-45deg);
      }
    }
  }

  &-menu {
    display: none;
    @include sp {
      position: absolute;
      width: 100%;
      height: calc(100vh - 40px);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      background-color: $white;
      top: 50px;
      right: -100%;
      overflow-y: scroll;
      transition: .5s;
      z-index: 999;
    }
    @include tablet {
      position: absolute;
      width: 100%;
      height: calc(100vh - 40px);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      background-color: $white;
      top: 50px;
      right: -100%;
      overflow-y: scroll;
      transition: .5s;
      z-index: 999;
    }
    @include small-pc {
      position: absolute;
      width: 100%;
      height: calc(100vh - 40px);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      background-color: $white;
      top: 50px;
      right: -100%;
      overflow-y: scroll;
      transition: .5s;
      z-index: 999;
    }
  }

  &-menu__animate {
    right: 0;
  }

  &-menu__contacts {
    display: flex;
    width: 100%;
    height: 80px;

    &-item {
      width: 100%;
      height: 80px;
      @include center;
    }
  }

  &-menu__dropdown {
    width: 100%;
  }

  &-menu__dropdown-item {
    font-family: $oswald;
    font-size: 2.4rem;
    text-align: center;
    cursor: pointer;

    &--animate {
      .wills-trusts__faq-arrow {
        transform: rotate(360deg) scale(-1);
      }

      .header-menu__sub-link {
        transition: 1s ease-in-out;
        max-height: 100vh;
      }
    }
  }

  &-menu__dropdown-link {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  &-menu__sub-link {
    max-height: 0;
    padding-right: 10px;
    overflow: hidden;
    display: flex;
    align-items: center;
    flex-direction: column;
    transition: 0.8s ease-in-out;
  }

  &-menu__link {
    padding: getsp375(25px) getsp375(40px);
    font-family: $oswald;
    font-size: 2.4rem;
    line-height: getem(getsp375(2), getsp375(4));
    text-align: center;
    cursor: pointer;

    &--sub {
      font-size: 1.6rem;
      padding: getsp375(15px) getsp375(30px);
    }

    &--dropdown {
      padding: getsp375(25px) 0 getsp375(40px) getsp375(40px); 
    }
  }
  
  &__search {
    .searchform {
      @include center;
      margin: 0;
      overflow: hidden;
      width: 200px;
      @include sp {
        width: 100%;
        margin: 0 auto;
      }
      @include tablet {
        width: 100%;
        margin: 0 auto;
      }
      @include small-pc {
        width: 100%;
        margin: 0 auto;
      }
    }

    label, input {
      vertical-align: baseline;
      color: $gold;
    }

    input[type="search"] {
      border: 1px solid $primary;
      border-width: 0 0 1px;
      background-color: transparent;
      font-family: $source;
      font-size: 1.8rem;
      letter-spacing: 0.04em;
      color: $primary;
      padding: 0.125em 0.225em;
      width: 85%;

      &::placeholder {
        color: $primary;
        @include sp {
          padding-left: 30px;
          color: $text;
        }
        @include tablet {
          padding-left: 30px;
          color: $text;
        }
        @include small-pc {
          padding-left: 30px;
          color: $text;
        }
      }

      @include sp {
        text-align: center;
        font-size: 2.4rem;
        font-family: $oswald;
      }
      @include tablet {
        text-align: center;
        font-size: 2.4rem;
        font-family: $oswald;
      }
      @include small-pc {
        text-align: center;
        font-size: 2.4rem;
        color: $text;
        font-family: $oswald;
      }
    }

    input[type="search"]:focus {
      color: $primary;
      border-color: $gold;
      outline: none;
    }
  }

  &__consultation {
    width: 300px;
    height: 105px;
    background-color: $primary;
    background-image: url('/img/pattern-01.png');
    @include center;
    flex-direction: column;
    @include sp {
      width: 100%;
      height: 100px;
      margin-top: -17px;
    }
    @include tablet {
      width: 100%;
      height: 100px;
      margin-top: -17px;
    }
    @include small-pc {
      width: 100%;
      height: 100px;
      margin-top: -17px;
    }

    &:hover {
      transition: 0.3s ease-in;
      opacity: 0.8;
    }
  }
  
  &__consultation-text {
    font-family: $oswald;
    font-weight: $medium;
    font-size: 2.5rem;
    letter-spacing: 0.1em;
    color: $gold;
  }

  &__consultation-num {
    font-family: $playfair;
    font-weight: $medium;
    font-size: 2.5rem;
    letter-spacing: 0.1em;
    color: $white;
    margin-top: 5px;
  }
}
      
// ANIMATION FOR HEADER
@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
