.response {
  &__wrapper {
    padding: 10px;
    @include center;
    background: $red;

    &:hover {
      .response__logo {
        transition: 0.5s ease-in-out;
        transform: scale(1.1);
      }
    }
  }

  &__text {
    font-family: $playfair;
    font-size: 2.5rem;
    color: $white;
    @include sp {
      font-size: 1.6rem;
    }
  }

  &__logo {
    margin: 20px;
    width: 30px;
    height: 30px;
    overflow: hidden;
    @include sp {
      margin: 10px;
      width: 20px;
      height: 20px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
}