.contact-lower {
  margin: 60px 0 0;
  @include sp {
    margin: 30px 0;
  }

  &__container {
    position: relative;
    width: 100%;
    padding: 60px;
    @include center;
    @include sp {
      padding: 10px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url('/img/contact-lower-bg.jpg');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      filter: grayscale(100%);
    }
  }

  &__image {
    width: 300px;
    @include sp {
      width: 100%;
      max-width: 400px;
    }
    @include tablet {
      width: 100%;
      max-width: 500px;
    }
    @include small-pc {
      width: 100%;
      max-width: 600px;
    }
  }

  &__wrap {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    width: 80%;
    @include sp {
      flex-direction: column;
      justify-content: center;
      padding: 10px;
      width: 100%;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.800);
    }
  }

  &__info {
    width: 40%;
    z-index: 5;
    @include sp {
      width: 100%;
    }
  }

  &__form {
    width: 60%;
    z-index: 5;
    @include sp {
      width: 100%;
    }
  }

  &__address-wrap {
    margin: 20px auto;
    @include sp {
      margin: 10px auto;
    }
  }

  &__address {
    font-family: $playfair;
    color: $black;
    font-size: 2.4rem;
    @include sp {
      font-size: 2rem;
    }

    &--small {
      font-size: 1.6rem;
      line-height: getem(1.6, 3.2);

      a {
        &:hover {
          transition: 0.3s ease-in-out;
          opacity: 0.5;
        }
      }
    }

    &--medium {
      font-size: 2.4rem;
      line-height: getem(2.4, 4.8);
      @include sp {
        font-size: 2rem;
      }
      a {
        &:hover {
          transition: 0.3s ease-in-out;
          opacity: 0.5;
        }
      }
    }

    &--big {
      font-size: 3.2rem;
      line-height: getem(3.2, 6.4);
      @include sp {
        font-size: 2.4rem;
      }
      a {
        &:hover {
          transition: 0.3s ease-in-out;
          opacity: 0.5;
        }
      }
    }

    &--form-title {
      font-size: 3.2rem;
      line-height: getem(3.2, 6.4);
      color: $gold;
      @include sp {
        font-size: 2.4rem;
      }
      @include tablet {
        font-size: 2.8rem;
      }
      @include small-pc {
        font-size: 3rem;
      }
    }
  }

  &__form-wrap {
    display: flex;
    margin: 20px;
    @include sp {
      flex-direction: column;
      margin: 10px;
    }
  }

  &__textbox {
    width: 100%;
    height: 50px;
    padding: 20px;
    font-size: 1.6rem;
    border: none;
    background-color: transparent;
    border-bottom: 2px solid $gold;
    @include sp {
      padding: 10px;
      height: 30px;
      font-size: 1.4rem;
    }

    &--small {
      width: 50%;
      @include sp {
        width: 100%;
        margin-bottom: 20px;
      }

      &:not(:last-child) {
        margin-right: 20px;
      }
    }
  }

  &__textarea {
    font-family: $source;
    width: 100%;
    height: 150px;
    padding: 20px;
    font-size: 1.8rem;
    border: none;
    resize: none;
    background-color: transparent;
    border-bottom: 2px solid $gold;
    @include sp {
      padding: 10px;
      height: 100px;
      font-size: 1.6rem;
    }
  }

  &__form-button-container {
    margin: 20px auto;
    max-width: 300px;
    width: 100%;
    @include sp {
      margin: 10px auto;
      width: 100%;
      max-width: 500px;
    }
  }
}