.arrow-top {
  position: fixed;
  bottom: 0;
  width: 100px;
  height: 100px;
  z-index: 9;
  opacity: 0;
  transform: translateY(100px);
  transition: all 0.5s ease;
  cursor: pointer;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &:hover {
    transition: 0.3s ease-in;
    opacity: 0.8;
  }
}

.showBtn {
  opacity: 1;
  transform: translateY(0);
}
