.about {
  margin: 145px auto 40px;
  max-width: 1318px;
  @include sp {
    margin: 100px auto 20px;
    padding: 0 4%;
    width: 100%;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    margin: 60px auto;
    @include sp {
      margin: 30px auto 0;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__info {
    width: 765px;
    @include sp {
      width: 100%;
    }

    &::before {
      content: '';
      background-image: url('/img/hex.png');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: right;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 0.2;
    }
  }

  &__image-container {
    width: 550px;
    height: 760px;
    @include center;
    flex-direction: column;
    margin-left: 30px;
    z-index: 2;
    @include sp {
      width: 80%;
      height: auto;
      margin: 40px 0 0;
    }

    &--who {
      width: 465px;
      margin-left: 0;
      margin-right: 60px;
      @include sp {
        display: none;
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__info-text {
    font-size: 1.8rem;
    text-align: justify;
    @include sp {
     font-size: 1.6rem;
    }

    &:not(:last-child) {
      margin-bottom: 45px;
      @include sp {
        margin-bottom: 20px;
      }
    }

    span {
      font-size: 2rem;
      color: $primary;
      @include sp {
        font-size: 1.6rem;
      }
    }

    &--white {
      color: $white;
    }

    &--bold-white {
      // font-family: $source;
      color: $white;
      font-weight: $bold;
      text-transform: capitalize;
    }

    &--gray {
      color: $gray;
    }
  }

  &__name {
    font-family: $playfair;
    font-size: 2.5rem;
    color: $primary;
    margin: 15px auto;
    @include sp {
      font-size: 2rem;
      margin: 10px auto;
    }
  }

  &__who {
    @include sp {
      padding: 0 4%;
    }
  }

  &__who-text-wrapper {
    width: 500px;
    @include sp {
      width: 100%;
      margin-top: 200px;
    }
  }

  &__who-wrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 40px;
    @include sp {
      padding: 20px 0;
      flex-direction: column;
      align-items: center;
    }
  }

  &__who-info {
    position: relative;
    width: auto;
    @include sp {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  &__who-logo {
    display: flex;
    width: 200px;
    height: 200px;
    @include sp {
      flex-wrap: wrap;
      justify-content: center;
      width: 150px;
      height: 150px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }

    &--second {
      margin-left: 50px;
      @include sp {
        margin-left: 0;
      }
    }
  }

  &__who-text-container {
    // position: absolute;
    // left: -20px;

    &--desc {
      margin-top: 60px;
      @include sp {
        margin-top: 30px;
      }
    }
  }

  &__who-info-title {
    position: relative;
    font-size: 3rem;
    font-family: $playfair;
    // color: $white;
    @include sp {
      font-size: 2.4rem;
    }

    &::after {
      content: '';
      display: block;
      position: absolute;
      left: -20%;
      width: 100%;
      height: 1px;
      margin-top: 20px;
      background-color: $primary;
    }
  }

  &__who-button-container {
    margin: 20px auto 0;
    @include sp {
      margin: 10px auto 0;
    }
  }

  &__community {
    position: relative;
    display: flex;
    // flex-wrap: wrap;
    margin: 60px auto;
    @include sp {
      flex-direction: column;
      margin: 30px auto;
      padding: 0 4%;
    }
  }

  &__community-title-container {
    min-width: 500px;
    @include sp {
      min-width: 0;
      width: 100%;
    }
  }

  &__community-title {
    position: relative;
    font-size: 3rem;
    font-family: $playfair;
    text-align: center;
    color: $primary;
    @include sp {
      font-size: 2rem;
      width: 100%;
      margin-bottom: 30px;
    }

    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 1px;
      margin-top: 20px;
      background-color: $primary;
      @include sp {
        margin-top: 10px;
      }
    }
  }

  &__community-text-container {
    margin: 60px 0 60px 200px;
    width: 565px;
    @include sp {
      margin: 20px 0;
      width: 100%;
    }
    @include tablet {
      margin: 40px;
      width: 500px;
    }
    @include small-pc {
      margin: 40px;
      width: 500px;
    }
  }

  &__community-text {
    font-size: 1.8rem;
    font-weight: $bold;
    text-align: justify;
    color: $dark-gray;
    margin: 20px auto;
    @include sp {
      margin: 10px auto;
      font-size: 1.6rem;
    }
  }

  &__community-details {
    margin-right: 40px;
    @include sp {
      margin-right: 0;
    }
  }

  &__community-logo {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    @include sp {
      flex-wrap: wrap;
      width: 100%;
    }
  }


  &__community-logo-img {
    width: 300px;
    height: 300px;
    object-fit: cover;
    object-position: center;
    margin-top: -1px;
    margin-left: -1px;
    overflow: hidden;
    @include sp {
      width: 50%;
      height: auto;

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
    @include tablet {
      width: 50%;
      height: auto;

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
    @include small-pc {
      width: 50%;
      height: auto;

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
    

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }

    &:hover {
      img {
        transition: 0.3s ease-in-out;
        transform: scale(1.1);
      }
    }
  }

  &__information {
    margin: 60px auto;
    @include sp {
      margin: 30px auto;
    }
  }

  &__information-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 25px;
    overflow: hidden;
    @include max-width;
  }

  &__information-item {
    position: relative;
    background-color: $primary;
    min-height: 628px;
    @include sp {
      min-height: 300px;
    }

    &:hover {
      .about__information-image img {
        transition: 0.3s ease-in-out;
        transform: scale(1.1);
      }

      .button-about-information {
        &:after,
        &:before {
          transform: translate(0);
        }
      }
    }
  }

  &__information-image {
    width: 100%;
    height: 225px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      opacity: 1;
    }
  }

  &__information-text {
    margin: 55px 40px;
    font-family: $source;
    font-weight: $regular;
    color: $white;
    @include center;
    flex-direction: column;
    padding: 20px;
    @include sp {
      margin: 30px auto 20px;
      padding: 20px;
    }

    h1 {
      font-size: 4rem;
      @include sp {
        font-size: 2.4rem;
      }
    }

    p {
      font-size: 1.6rem;
      line-height: getem(16, 32);
      margin: 35px 0;
      @include sp {
        margin: 20px 0;
      }
    }
  }

  &__information-btn-container {
   margin: auto;
  }
}