.contact {
  &__container {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    height: 100vh;
    background-image: 
    linear-gradient(
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
    url('/img/contact-bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    padding: 200px 100px 0;
    @include sp {
      height: auto;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding: 100px 4%;
    }
  }

  &__info {
    width: 50%;
    margin-right: 40px;
    @include sp {
      width: 100%;
      margin-right: 0;
    }
  }

  &__info-title {
    font-size: 7rem;
    font-weight: $bold;
    color: $gold;
    @include sp {
      font-size: 4rem;
    }

    &--small {
      font-size: 4.8rem;
      @include sp {
        font-size: 3.5rem;
      }
    }
  }

  &__info-text {
    font-family: $oswald;
    font-size: 4.8rem;
    color: $white;
    word-wrap: break-word;
    @include sp {
      font-size: 3.5rem;
    }

    &--small {
      font-size: 2.4rem;
      text-align: justify;
      font-family: $oswald;
      @include sp {
        font-size: 2rem;
      }
    }
  }

  &__info-text-wrapper {
    margin: 20px 0;
    @include sp {
      margin: 10px 0;
    }
  }

  &__form {
    width: 50%;
    @include sp {
      width: 100%;
    }
  }

  &__form-wrapper {
    display: flex;
    flex-direction: column;
    margin: 10px 0 0;
  }

  &__form-button-container {
    margin: 20px auto;
    max-width: 500px;
    width: 100%;
    @include sp {
      margin: 10px auto;
      width: 100%;
      max-width: 500px;
    }
  }

  &__textbox {
    margin-bottom: 10px;
    height: 57px;
    font-family: $source;
    font-size: 2rem;
    padding: 10px;
    &::placeholder {
      color: $gray;
    }
    @include sp {
      height: 40px;
      margin-bottom: 5px;
      font-size: 1.6rem;
    }
  }

  &__textarea {
    height: 220px;
    font-family: $source;
    font-size: 2rem;
    padding: 10px;
    &::placeholder {
      color: $gray;
    }
    @include sp {
      height: 110px;
      font-size: 1.6rem;
    }
  }

  &__map-container {
    @include center;
    margin: 60px auto;
    @include sp {
      flex-direction: column;
      margin: 30px auto;
      padding: 0 4%;
    }
  }
  
  &__map {
    max-width: 765px;
    width: 100%;
    height: 553px;
    box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;
    @include sp {
      max-width: none;
      width: 100%;
      height: 300px;
    }

     iframe {
      max-width: 765px;
      width: 100%;
      height: 553px;
      border-radius: 20px;
      @include sp {
        border-radius: 0;
        max-width: none;
        width: 100%;
        height: 300px;
      }
     }
  }
  
  &__map-info {
    position: relative;
    width: 400px;
    height: 500px;
    background-color: $primary;
    background-image: url('/img/pattern-01.png');
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 60px;
    @include sp {
      max-width: none;
      align-items: center;
      width: 100%;
      height: 300px;
      padding: 30px;
      margin: 30px auto;
    }
  }

  &__map-info-text {
    font-family: $oswald;
    font-size: 2.4rem;
    font-weight: $light;
    color: $white;
    @include sp {
      max-width: none;
      font-size: 2rem;
    }
  }

  &__map-info-phone {
    font-family: $oswald;
    font-size: 2rem;
    font-weight: $light;
    color: $white;
    margin: 40px 0 65px;
    @include sp {
      max-width: none;
      font-size: 1.8rem;
      margin: 20px 0 35px;
    }

    &:hover {
      transition: 0.3s ease-in-out;
      opacity: 0.5;
    }
  }

  &__map-info-loc {
    position: absolute;
    top: 50px;
    left: -50px;
    width: 193px;
    height: 60px;
    background-color: $gold;
    @include center;
    color: $white;
    @include sp {
      top: -20px;
      left: 50%;
      transform: translateX(-50%);
      width: 100px;
      height: 40px;
    }
    @include tablet {
      top: -20px;
      left: 50%;
      transform: translateX(-50%);
    }
    @include small-pc {
      top: -20px;
      left: 50%;
      transform: translateX(-50%);
    }

    span {
      font-size: 2.4rem;
      color: $white;
      @include sp {
        font-size: 1.8rem;
      }
    }
  }
}