.equine {
  margin: 145px auto 40px;
  max-width: 1318px;
  @include sp {
    margin: 100px auto 20px;
    padding: 0 4%;
    width: 100%;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    margin: 60px 20px;
    @include sp {
      margin: 30px auto 10px;
      flex-direction: column;
      justify-content: center;
    }
  }

  &__info {
    width: 765px;
    @include sp {
      width: 100%;
    }

    &::before {
      content: '';
      background-image: url('/img/hex.png');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: right;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 0.2;
    }
  }

  &__image-container {
    width: 600px;
    height: 600px;
    @include center;
    flex-direction: column;
    margin-left: 30px;
    z-index: 2;
    @include sp {
      width: 100%;
      height: auto;
      margin-left: 0;
      margin-top: 20px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }
  }

  &__info-text {
    font-size: 1.8rem;
    text-align: justify;
    @include sp {
      font-size: 1.6rem;
    }

    &:not(:last-child) {
      margin-bottom: 45px;
      @include sp {
        margin-bottom: 25px;
      }
    }

    span {
      font-size: 2rem;
      color: $primary;
      @include sp {
        font-size: 1.6rem;
      }
    }

    &--white {
      color: $white;
    }

    &--bold-white {
      // font-family: $source;
      color: $white;
      font-weight: $bold;
      text-transform: capitalize;
    }

    &--gray {
      color: $gray;
    }
  }
}