.top {
  max-width: 160rem;
  margin: 0 auto;
  overflow: hidden;
  @include sp {
    max-width: 100%;
  }
  
  //SERVICES
  &__services {
    margin-top: 100px;
    @include sp {
      margin-top: getsp375(100px);
      // padding: 0 4%;
    }

    &-container {
      padding: 1.5rem;
      margin: 3rem auto;
      @include sp {
        padding: getsp375(20px);
        margin: getsp375(30px) auto;
      }
    }

    &-image {
      width: 100%;
      height: 250px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }
    
    &-cards {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
      grid-gap: 1.5rem;
    }
    
    &-card {
      font-family: sans-serif;
      color: #fff;
      padding: 1.5rem;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      background-color: rgba(255, 255, 255, 0.959);
      border: 1px solid $gray;

      &:hover {
        background-color: $primary;
        opacity: 1;

        .top__services-card-title-small,
        .top__services-card-title-large,
        .top__services-card-description {
          color: $white;
        }

        .animated-arrow {
          color: $white;
          
          > .the-arrow.-left {
            > .shaft {
              width: $shaft-width;
              transition-delay: 0.1s;
              background-color: $white;
      
              &::before,
              &::after {
                width: $arrow-head-width;
                transition-delay: 0.1s;
                background-color: $white;
              }
      
              &::before {
                transform: rotate(40deg);
              }
      
              &::after {
                transform: rotate(-40deg);
              }
            }
          }
          
          > .main {
            transform: translateX($shaft-width + $text-arrow-space);
            
            > .the-arrow.-right {
              > .shaft {
                width: 0;
                transform: translateX(200%);
                transition-delay: 0;
                
                &::before,
                &::after {
                  width: 0;
                  transition-delay: 0;
                  transition: all 0.1s;
                }
      
                &::before {
                  transform: rotate(0);
                }
      
                &::after {
                  transform: rotate(0);
                }
              }
            }
          }
        }
      }
    }
    
    &-card-title-large {
      font-size: 4.8rem;
      letter-spacing: .5rem;
      // transform: translateX(5rem);
      color: #58575717;
      transition: transform 2s;
      white-space: nowrap;
      margin: 10px auto;
      @include sp {
        font-size: 3.5rem;
      }
    }
    
    &-card-title-small {
      font-size: 1.6rem;
      margin-bottom: 1rem;
      color: #1d1d1d;
    }
    
    &-card-description { //for future purpose
      font-size: 1.6rem;
      line-height: 1.5;
      color: #1d1d1d;
    }
    
    &-card-cta  {
      display: inline-block;
      text-decoration: none;
      color: #fff;
      margin-top: 1rem;
      align-self: flex-end;
    }
    
    .top__services-card:hover .top__services-card-title-large{
      transform: translateX(-100%);
      color: #fff;
    }
  }

  //BADGES
  &__badges {
    display: block;
    width: 1570px;
    margin: 100px auto;
    @include sp {
      width: 100%;
      margin: getsp375(100px) auto;
      padding: 0 4%;
    }

    &-slider {
      width: 1570px;
      @include sp {
        width: 100%;
      }
    }

    &-image {
      width: 250px;
      height: 250px;
      @include sp {
        width: 100%;
      }

      &:not(:last-child) {
        margin-right: 80px;
        @include sp {
          margin-right: 0;
        }
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }

    &-custom-nav {
      position: relative;
      width: 250px;
      margin: 50px auto;
      @include center;
      @include sp {
        margin: getsp375(100px) auto;
      }

      .carousel--prev, .carousel--next {
        padding: 5px;
        margin: 2px;
        &:hover {
          cursor: pointer;
          opacity: 0.5;
        }
      
        &:active {
          color: $gold;
        }
      }

      .carousel--prev {
        transform: scaleX(-1);
      }
    }
  
    &-custom-wrap {
      width: 180px;
      margin: 0 auto;
      @include sp {
        margin: 0 getsp375(30px);
      }
  
      img {
        display: block;
        margin: 0 auto;
      }
    }
  }

  //ABOUT
  &__about {
    display: flex;
    justify-content: space-between;
    @include sp {
      flex-direction: column;
      padding: 0 4%;
    }
  }

  &__about-wrapper {
    width: 50%;
    @include sp {
      width: 100%;
    }

    &--text {
      margin: 0 60px 0 40px;
      @include sp {
        margin: 0;
      }
    }
  }

  &__about-text-container {
    margin: 30px auto;
    @include sp {
      margin: getsp375(30px) auto;
    }
  }

  &__about-text {
    font-size: 2rem;
    text-align: justify;
    @include sp {
      font-size: getsp375(32px);
    }
  }

  &__about-button-container {
    position: relative;
    margin: 50px auto;
    @include sp {
      margin: getsp375(50px) auto;
      width: 100%;
    }

    &--image {
      position: absolute;
      bottom: 0;
      left: 0;
      margin: 0;
      width: 100%;
    }
  }

  &__about-image {
    position: relative;
    display: block;
    margin: 0 auto;
    width: 100%;
    height: 700px;
    @include sp {
      height: getsp375(600px);
    }
    
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }

    &::before {
      content: '';
      position: absolute;
      top: 20px;
      left: -20px;
      width: 100%;
      height: 700px;
      background-color: $gold;
      z-index: -1;
      @include sp {
        top: 10px;
        left: -10px;
        height: getsp375(600px);
      }
    }
  }

  &__about-wrapper-second {
    margin: 40px auto;
    display: flex;
    flex-direction: row-reverse;
    @include sp {
      flex-direction: column;
      margin: getsp375(100px) auto;
      padding: 0 4%;
    }
  }

  &__planning {
    margin: 85px auto;
    width: 100%;
    display: flex;
    justify-content: center;
    overflow: hidden;
    @include sp {
      margin: getsp375(85px) auto;
      flex-direction: column;
      align-items: center;
      padding: 0 4%;
    }
  }

  &__planning-wrapper {
    width: 800px;
    @include tablet {
      width: 600px;
    }
    @include sp {
      width: 100%;
    }

    &--second {
      width: 500px;
      text-align: center;
      margin-left: 40px;
      @include sp {
        width: 100%;
        margin-left: 0;
      }
    }
  }

  &__planning-title {
    font-family: $playfair;
    font-size: 3.5rem;
    font-weight: $bold;
    color: $primary;
    @include sp {
      font-size: getsp375(4rem);
      width: 100%;
      margin-left: 0;
    }

    &--small {
      font-family: $source;
      font-size: 2.5rem;
      margin-bottom: 15px;
      @include sp {
        font-size: getsp375(3rem);
        width: 100%;
        margin-bottom: getsp375(15px);
      }
    }
  }

  &__planning-text {
    font-size: 2rem;
    @include sp {
      font-size: getsp375(3rem);
    }

    &--small {
      font-size: 1.6rem;
      text-align: justify;
    }
  }

  &__planning-text-wrapper {
    margin: 20px auto;
    @include sp {
      margin: 10px auto;
    }
  }

  &__planning-info-wrapper {
    display: flex;
    @include sp {
      @include center;
      flex-direction: column;
    }
  }

  &__planning-info {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    width: 380px;
    @include sp {
      width: 100%;
      margin-bottom: 20px;
    }

    img {
      width: 150px;
      @include sp {
        width: 40%;
        max-width: 100px;
      }
    }

    &--second {
      margin-left: 40px;
      @include sp {
        margin-left: 0;
      }
    }
  }

  &__planning-image {
    width: 500px;
    height: 500px;
    margin-top: 50px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }

    &:hover {
      img {
        transition: 0.3s ease-in;
        transform: scale(1.1);
      }
    }
  }

  &__contact {
    margin: 80px auto -60px;
    @include sp {
      margin: 40px auto -30px;
      padding: 0 4%;
    }
  }

  &__contact-wrapper {
    //
  }
}
