//BADGES SECTION
.top__badges-slider {
  position: relative;
  width: 100%;
  @include sp {
    height: getsp375(450px);
    padding: 0;
  }

  .flickity-prev-next-button {
    display: none;
  }

  &-index {
    font-family: $source;
    font-weight: $light;
    font-size: 42px;
    text-align: center;
    @include sp {
      top: getsp375(10px);
      left: getsp375(72px);
      font-size: getsp375(42px);
    }
  }
  
  &-array {
    font-family: $source;
    font-weight: $light;
    font-size: 42px;
    text-align: center;
    @include sp {
      bottom: getsp375(5px);
      right: getsp375(70px);
      font-size: getsp375(42px);
    }
  }
}