.title {
  h1 {
    position: relative;
    padding: 0;
    margin: 0;
    font-family: "Raleway", sans-serif;
    font-weight: 300;
    font-size: 40px;
    color: #080808;
    -webkit-transition: all 0.4s ease 0s;
    -o-transition: all 0.4s ease 0s;
    transition: all 0.4s ease 0s;
  }
  
  h1 span {
    display: block;
    font-size: 0.5em;
    line-height: 1.3;
  }
  h1 em {
    font-style: normal;
    font-weight: 600;
  }

  &-heading {
    text-align: center; 
    font-size: 5rem; 
    text-transform: uppercase; 
    color:#222;
    letter-spacing:1px;
    font-family: $playfair; 
    font-weight:400;
    @include sp {
      font-size: 3rem;
    }

    span {
      margin-top: 5px;
      font-size: 1.5rem; color:#444;
      word-spacing:1px;
      font-weight:normal;
      letter-spacing:2px;
      text-transform: uppercase;
      font-family:"Raleway", sans-serif;
      font-weight:500;
      display: grid;
      grid-template-columns: 1fr max-content 1fr;
      grid-template-rows: 27px 0;
      grid-gap: 20px;
      align-items: center;

      &::before,
      &::after {
        content: " ";
        display: block;
        border-bottom: 1px solid #ccc;
        border-top: 1px solid #ccc;
        height: 5px;
        background-color:#f8f8f8;
      }
    }
  }

  &-section {
    font-size: 2.8rem;
    font-weight: 500;
    letter-spacing: 0;
    line-height: getem(28px, 56px);
    padding-bottom: 15px;
    position: relative;
    color: $primary;
    @include sp {
      font-size: 2.4rem;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      height: 5px;
      width: 55px;
      background-color: $primary;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 2px;
      height: 1px;
      width: 95%;
      max-width: 255px;
      background-color: $primary;
    }

    &--small {
      font-size: 2.2rem;
      font-weight: $light;
      color: $gray;
      @include sp {
        font-size: 1.8rem;
      }

      span {
        font-family: $source;
        font-size: 5rem;
        font-weight: 700;
        color: $primary;
        @include sp {
          font-size: 4rem;
        }
      }
    }
  }

  &-subheading {
    position: relative;
    font-size: 3rem;
    text-align: center; 
    padding-bottom: 45px;
    font-family: $playfair;
    text-transform: uppercase;
    letter-spacing: 2px;
    color:#111;
    @include sp {
      font-size: 2rem;
      word-wrap: break-word;
    }

    &::before {
      position: absolute;
      left: 0;
      bottom: 20px;
      width: 60%;
      left: 50%;
      margin-left:-30%;
      height: 1px;
      content: "";
      background-color: #777;
      z-index: 4;
    }

    &::after {
      position:absolute;
      width:40px;
      height:40px;
      left:50%;
      margin-left:-20px;
      bottom: 0;
      content: '\2696';
      font-size:30px;
      line-height:40px;
      color: $gold;
      font-weight:400;
      z-index: 5;
      display:block;
      background-color: $white;
    }
  }

  &-question {
    position: relative;
    font-size: 3rem;
    text-align: center; 
    line-height: 1.5em; 
    padding-bottom: 45px;
    font-family: $playfair;
    text-transform: uppercase;
    letter-spacing: 2px;
    color:#111;
    @include sp {
      font-size: 2.4rem;
    }

    &::before {
      position: absolute;
      left: 0;
      bottom: 20px;
      width: 60%;
      left: 50%;
      margin-left:-30%;
      height: 1px;
      content: "";
      background-color: #777;
      z-index: 4;
    }

    &::after {
      position:absolute;
      width:40px;
      height:40px;
      left:50%;
      margin-left:-20px;
      bottom: 0;
      content: '?';
      font-size:30px;
      line-height:40px;
      color: $gold;
      font-weight:400;
      z-index: 5;
      display:block;
      background-color: $white;
    }
  }

  &-service-card {
    text-align: center;
    text-transform: uppercase;
    padding-bottom: 5px;

    &::before {
      width: 28px;
      height: 5px;
      display: block;
      content: "";
      position: absolute;
      bottom: 3px;
      left: 50%;
      margin-left: -14px;
      background-color: #b80000;
    }

    &::after {
      width: 100px;
      height: 1px;
      display: block;
      content: "";
      position: relative;
      margin-top: 25px;
      left: 50%;
      margin-left: -50px;
      background-color: #b80000;
    }
  }

  &-service {
    font-size: 3.5rem !important;
    font-weight: $regular;
    text-align: center;
    padding-bottom: 5px;
    text-align: left;
    @include sp {
      font-size: 3rem !important;
    }

    &::after {
      width: 50%;
      height: 1px;
      display: block;
      content: "";
      position: relative;
      margin-top: 25px;
      left: 50%;
      background-color: $primary;
      @include sp {
        margin-top: 15px;
      }
    }
  }
}