.services {

  &__wrapper {
    //
  }

  &__hero {
    background-image: url('/img/services-hero-image.jpg');
    background-attachment: fixed;
    background-position-y: -0.2;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    color: white;
    transition: 0s linear;
    transition-property: background-position;
    height: 100vh;
    @include center;
    @include sp {
      height: 100%;
    }
  }

  &__hero-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin: 0 100px;
    @include sp {
      flex-direction: column-reverse;
      justify-content: center;
      margin: 50px 25px;
    }
  }

  &__hero-info {
    text-align: left;
    // text-shadow: 2px 2px $gold;
    @include sp {
      text-align: center;
    }

    h1 {
      font-family: $playfair;
      font-size: 6rem;
      line-height: getem(60, 100);
      font-weight: $bold;
      @include sp {
        font-size: 4rem;
      }
    }

    h2 {
      font-family: $playfair;
      font-size: 4rem;
      line-height: getem(40, 80);
      @include sp {
        font-size: 3rem;
      }
    }

    p {
      font-family: $source;
      font-size: 2rem;
      line-height: getem(20, 40);
      font-weight: $bold;
      @include sp {
        font-size: 1.8rem;
      }
    }
  }

  &__hero-info-image {
    width: 800px;
    height: auto;
    overflow: hidden;
    @include sp {
      width: 100%;
      max-width: 500px;
    } 

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }
  }

  &__hero-btn-container {
    margin: 20px 0;
    @include sp {
      @include center;
    }
  }

  &__intro {
    margin: 60px 0;
    @include sp {
      margin: 30px 0;
    }

    &--service-item {
      margin: 100px 0;
      @include sp {
        margin: 50px 0;
      }
    }
  }

  &__intro-wrapper {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    // @include max-width;
    @include sp {
      justify-content: center;
      flex-direction: column;
      padding: 0 4%;
    }
  }

  &__intro-desc {
    width: 30%;
    margin-right: 5%;
    @include sp {
      width: 100%;
      margin-right: 0
    }

    &--lines {
      @include center;
      background-image: url('/img/wavy-lines-bg.png');
      background-repeat: repeat;
      background-size: contain;
      transition: 0s linear;
      transition-property: background-position;
      width: 50%;
      height: 470px;
      margin-right: 0;
      margin-left: 100px;
      @include sp {
        width: 100%;
        height: 100%;
        margin-left: 0;
      }
    }
  }
  
  &__intro-text {
    margin: 20px 0;
    @include sp {
      margin: 10px 0;
    }

    p {
      font-size: 1.6rem;
      text-align: justify;
    }

    h1 {
      font-family: $playfair;
      font-weight: $regular;
      font-size: 2.4rem;
      width: 100%;
      margin-left: -100px;
      @include sp {
        font-size: 1.8rem;
        margin-left: 0;
      }
    }
  }

  &__intro-lawyer-type {
    position: relative;
    background-color: $primary;

    &::before {
      content: '';
      background-image: url('/img/abstract-bg.png');
      background-size: contain;
      background-repeat: repeat;
      background-position: center;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 0.1;
    }
  }

  &__intro-lawyer-wrapper {
    @include center;
    padding: 70px;
    @include max-width;
    @include sp {
      flex-direction: column;
      padding: 35px;
    }

    &--service-item {
      max-width: 1600px;
      @include sp {
        max-width: 100%;
      }
    }
  }

  &__intro-lawyer-text {
    width: 315px;
    @include sp {
      width: 100%;
    }

    h1 {
      font-family: $playfair;
      font-size: 4rem;
      line-height: getem(40, 60);
      color: $white;
      @include sp {
        font-size: 2.4rem;
      }
    }

    p {
      font-family: $source;
      font-size: 2rem;
      line-height: getem(20, 40);
      color: $white;
      margin-top: 20px;
      @include sp {
        font-size: 1.6rem;
        margin-top: 10px;
      }
    }
  }

  &__intro-lawyer-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    // grid-template-columns: repeat(auto-fill, minmax(330px, 1fr));
    margin: 0 auto;
    width: 702px;
    grid-gap: 40px;
    margin-left: 40px;
    // align-items: stretch;
    @include sp {
      width: 100%;
      grid-gap: 20px;
      margin-left: 0;
      margin-top: 20px;
    }

    &--service-item {
      width: 100%;
      margin-left: 0;
    }
  }

  &__intro-lawyer-item {
    &:hover {
      .services__intro-lawyer-desc{
        &::before,
        &::after {
          width: 100%;
          height: 100%;
        }
      }

      .services__intro-lawyer-image img {
        transition: 0.5s ease-in-out;
        transform: scale(1.1);
      }
    }
  }

  &__intro-lawyer-image {
    position: relative;
    display: block;
    transition: transform 0.3s cubic-bezier(0.374, 0.019, 0.035, 1.861);
    background-color: $white;
    width: 330px;
    height: 330px;
    overflow: hidden;
    @include sp {
      width: 280px;
      height: 280px;
    }

    &::after {
      content: '';
      background: rgba(0, 0, 0, 0.5);
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__intro-lawyer-desc {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: $playfair;
    color: white;
    font-weight: $regular;
    font-size: 40px;
    text-align: center;
    z-index: 6;
    width: 280px;
    height: 280px;
    @include center;
    flex-direction: column;
    @include sp {
      width: 220px;
      height: 220px;
    }
    
    &::before,
    &::after {
      position: absolute;
      content: "";
      transition: all .25s;
    }

    &::before {
      border-bottom: 2px solid $gold;
      border-left: 2px solid $gold;
      width: 20%;
      height: 33%;
      left: -10px;
      bottom: -10px;
    }

    &::after {
      border-top: 2px solid $gold;
      border-right: 2px solid $gold;
      width: 20%;
      height: 33%;
      top: -10px;
      right: -10px;
    }
  }

  &__injury {
    margin: 60px auto;
    @include sp {
      margin: 30px auto;
    }
  }

  &__injury-container {
    display: flex;
    justify-content: center;
    @include max-width;
    @include sp {
      flex-direction: column;
    }
  }

  &__injury-image {
    width: 510px;
    height: 510px;
    overflow: hidden;
    @include sp {
      width: 100%;
      height: auto;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__injury-desc {
    position: relative;
    width: 510px;
    margin-left: 40px;
    @include sp {
      width: 100%;
      margin-left: 0;
    }
  }

  &__injury-text {
    font-family: $source;
    font-size: 2.2rem;
    color: $dark-gray;
    @include sp {
      font-size: 1.6rem;
    }

    &--title {
      font-family: $playfair;
      color: $primary;
      font-size: 4.2rem;
      @include sp {
        font-size: 3rem;
        margin: 10px 0;
      }
    }

    &--desc {
      color: $text;
      margin: 20px 0;
      text-align: justify;
      @include sp {
        margin: 10px 0;
      }
    }

    &--bullet {
      color: $text;
      line-height: getem(16, 22);
      margin: 20px 0 0 20px;
      text-align: justify;
      @include sp {
        margin: 10px 0 0 12px;

      }

      &::before {
        content: '';
        position: absolute;
        left: 8px;
        @include sp {
          left: 4px;
        }
      }
    }
  }
}